'use client';

import React, { useState, useEffect, useCallback } from 'react';
import { generateLetters, isValidWord, calculateScore, getWordHint } from '@/lib/wordmania';
import useTimer from '@/hooks/useTimer';

interface GameStats {
  totalWords: number;
  longestWord: string;
  averageWordLength: number;
}

export default function WordManiaGame() {
  const [letters, setLetters] = useState<string[]>([]);
  const [currentWord, setCurrentWord] = useState<string>('');
  const [validWords, setValidWords] = useState<string[]>([]);
  const [score, setScore] = useState<number>(0);
  const [error, setError] = useState<string>('');
  const [gameMode, setGameMode] = useState<'endless' | 'timed'>('endless');
  const [showHint, setShowHint] = useState<boolean>(false);
  const [hint, setHint] = useState<string>('');
  const [gameStats, setGameStats] = useState<GameStats>({
    totalWords: 0,
    longestWord: '',
    averageWordLength: 0
  });

  const timer = useTimer(180); // 3 minutes for timed mode

  useEffect(() => {
    const newLetters = generateLetters();
    setLetters(newLetters);
    setHint(getWordHint(newLetters));
  }, []);

  useEffect(() => {
    if (timer.isTimeUp && gameMode === 'timed') {
      // Game over logic for timed mode
      setError('Time\'s up! Game over.');
    }
  }, [timer.isTimeUp, gameMode]);

  const updateGameStats = useCallback((newWord: string, allWords: string[]) => {
    const totalLength = allWords.reduce((sum, word) => sum + word.length, 0);
    const longestWord = allWords.reduce((longest, word) => 
      word.length > longest.length ? word : longest, '');
    
    setGameStats({
      totalWords: allWords.length,
      longestWord,
      averageWordLength: allWords.length > 0 ? Math.round(totalLength / allWords.length * 10) / 10 : 0
    });
  }, []);

  const handleLetterClick = useCallback((letter: string) => {
    if (timer.isTimeUp && gameMode === 'timed') return;
    
    setCurrentWord(prev => prev + letter);
    setError('');
  }, [timer.isTimeUp, gameMode]);

  const handleBackspace = useCallback(() => {
    if (timer.isTimeUp && gameMode === 'timed') return;
    
    setCurrentWord(prev => prev.slice(0, -1));
    setError('');
  }, [timer.isTimeUp, gameMode]);

  const handleSubmit = useCallback(() => {
    if (timer.isTimeUp && gameMode === 'timed') return;
    
    if (!currentWord) {
      setError('Please enter a word first.');
      return;
    }

    if (currentWord.length < 3) {
      setError('Words must be at least 3 letters long.');
      setCurrentWord('');
      return;
    }

    if (validWords.includes(currentWord.toUpperCase())) {
      setError('Word already used! Try a different word.');
      setCurrentWord('');
      return;
    }

    if (isValidWord(currentWord)) {
      const newValidWords = [...validWords, currentWord.toUpperCase()];
      const wordScore = calculateScore(currentWord);
      
      setValidWords(newValidWords);
      setScore(prev => prev + wordScore);
      setError('');
      updateGameStats(currentWord, newValidWords);
      
      // Show success message briefly
      setError(`Great! +${wordScore} points`);
      setTimeout(() => setError(''), 2000);
    } else {
      setError('Invalid word. Try again!');
    }
    
    setCurrentWord('');
  }, [currentWord, validWords, timer.isTimeUp, gameMode, updateGameStats]);

  const handleReset = useCallback(() => {
    const newLetters = generateLetters();
    setLetters(newLetters);
    setCurrentWord('');
    setValidWords([]);
    setScore(0);
    setError('');
    setHint(getWordHint(newLetters));
    setGameStats({ totalWords: 0, longestWord: '', averageWordLength: 0 });
    timer.reset();
  }, [timer]);

  const handleGameModeChange = useCallback((mode: 'endless' | 'timed') => {
    setGameMode(mode);
    if (mode === 'timed') {
      timer.start();
    } else {
      timer.stop();
    }
  }, [timer]);

  const handleShowHint = useCallback(() => {
    setShowHint(true);
    setTimeout(() => setShowHint(false), 3000);
  }, []);

  const handleKeyPress = useCallback((event: KeyboardEvent) => {
    if (event.key === 'Enter') {
      handleSubmit();
    } else if (event.key === 'Backspace') {
      handleBackspace();
    } else if (event.key.match(/[a-zA-Z]/) && event.key.length === 1) {
      const letter = event.key.toUpperCase();
      if (letters.includes(letter)) {
        handleLetterClick(letter);
      }
    }
  }, [handleSubmit, handleBackspace, handleLetterClick, letters]);

  useEffect(() => {
    window.addEventListener('keydown', handleKeyPress);
    return () => window.removeEventListener('keydown', handleKeyPress);
  }, [handleKeyPress]);

  return (
    <div className="min-h-screen bg-gradient-to-br from-indigo-900 via-purple-900 to-pink-900 p-4">
      <div className="max-w-4xl mx-auto">
        {/* Header */}
        <div className="text-center mb-8">
          <h1 className="text-5xl font-bold text-white mb-2">WordMania</h1>
          <p className="text-xl text-purple-200">Build words from the letters below</p>
        </div>

        {/* Game Mode Selector */}
        <div className="flex justify-center mb-6">
          <div className="bg-white/10 backdrop-blur-sm rounded-xl p-2 flex space-x-2">
            <button
              onClick={() => handleGameModeChange('endless')}
              className={`px-6 py-2 rounded-lg font-semibold transition-all ${
                gameMode === 'endless'
                  ? 'bg-white text-purple-900 shadow-lg'
                  : 'text-white hover:bg-white/20'
              }`}
            >
              Endless Mode
            </button>
            <button
              onClick={() => handleGameModeChange('timed')}
              className={`px-6 py-2 rounded-lg font-semibold transition-all ${
                gameMode === 'timed'
                  ? 'bg-white text-purple-900 shadow-lg'
                  : 'text-white hover:bg-white/20'
              }`}
            >
              Timed Mode (3 min)
            </button>
          </div>
        </div>

        <div className="grid lg:grid-cols-3 gap-8">
          {/* Game Board */}
          <div className="lg:col-span-2">
            <div className="bg-white/10 backdrop-blur-sm rounded-2xl p-6 shadow-2xl">
              {/* Score and Timer */}
              <div className="flex justify-between items-center mb-6">
                <div className="text-center">
                  <p className="text-3xl font-bold text-white">{score}</p>
                  <p className="text-purple-200">Score</p>
                </div>
                {gameMode === 'timed' && (
                  <div className="text-center">
                    <p className={`text-3xl font-bold ${timer.seconds <= 30 ? 'text-red-400' : 'text-white'}`}>
                      {timer.formatTime}
                    </p>
                    <p className="text-purple-200">Time Left</p>
                  </div>
                )}
                <div className="text-center">
                  <p className="text-3xl font-bold text-white">{validWords.length}</p>
                  <p className="text-purple-200">Words Found</p>
                </div>
              </div>

              {/* Letter Grid */}
              <div className="grid grid-cols-4 gap-3 mb-6">
                {letters.map((letter, index) => (
                  <button
                    key={index}
                    onClick={() => handleLetterClick(letter)}
                    disabled={timer.isTimeUp && gameMode === 'timed'}
                    className="aspect-square bg-gradient-to-br from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700 text-white text-2xl font-bold rounded-xl transition-all duration-200 transform hover:scale-105 active:scale-95 shadow-lg disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    {letter}
                  </button>
                ))}
              </div>

              {/* Current Word Display */}
              <div className="mb-6">
                <div className="bg-white/20 rounded-xl p-4 min-h-[60px] flex items-center justify-center">
                  <p className="text-2xl font-bold text-white tracking-wider">
                    {currentWord || 'Click letters to build a word...'}
                  </p>
                </div>
              </div>

              {/* Action Buttons */}
              <div className="flex flex-wrap gap-3 mb-4">
                <button
                  onClick={handleBackspace}
                  disabled={!currentWord || (timer.isTimeUp && gameMode === 'timed')}
                  className="flex-1 min-w-[120px] bg-orange-500 hover:bg-orange-600 text-white font-semibold py-3 px-6 rounded-xl transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  Backspace
                </button>
                <button
                  onClick={handleSubmit}
                  disabled={!currentWord || (timer.isTimeUp && gameMode === 'timed')}
                  className="flex-1 min-w-[120px] bg-green-500 hover:bg-green-600 text-white font-semibold py-3 px-6 rounded-xl transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  Submit Word
                </button>
                <button
                  onClick={handleReset}
                  className="flex-1 min-w-[120px] bg-red-500 hover:bg-red-600 text-white font-semibold py-3 px-6 rounded-xl transition-all duration-200"
                >
                  New Game
                </button>
              </div>

              {/* Hint Button */}
              <div className="text-center">
                <button
                  onClick={handleShowHint}
                  className="bg-yellow-500 hover:bg-yellow-600 text-white font-semibold py-2 px-4 rounded-lg transition-all duration-200"
                >
                  Show Hint
                </button>
                {showHint && hint && (
                  <p className="mt-2 text-yellow-300 font-semibold">
                    Try making: {hint}
                  </p>
                )}
              </div>

              {/* Error/Success Message */}
              {error && (
                <div className={`mt-4 p-3 rounded-lg text-center font-semibold ${
                  error.includes('Great!') || error.includes('points')
                    ? 'bg-green-500/20 text-green-300 border border-green-500/30'
                    : 'bg-red-500/20 text-red-300 border border-red-500/30'
                }`}>
                  {error}
                </div>
              )}
            </div>
          </div>

          {/* Sidebar */}
          <div className="space-y-6">
            {/* Game Stats */}
            <div className="bg-white/10 backdrop-blur-sm rounded-2xl p-6 shadow-2xl">
              <h2 className="text-2xl font-bold text-white mb-4">Game Stats</h2>
              <div className="space-y-3">
                <div className="flex justify-between">
                  <span className="text-purple-200">Total Words:</span>
                  <span className="text-white font-semibold">{gameStats.totalWords}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-purple-200">Longest Word:</span>
                  <span className="text-white font-semibold">{gameStats.longestWord || '-'}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-purple-200">Avg Length:</span>
                  <span className="text-white font-semibold">{gameStats.averageWordLength || '-'}</span>
                </div>
              </div>
            </div>

            {/* Words Found */}
            <div className="bg-white/10 backdrop-blur-sm rounded-2xl p-6 shadow-2xl">
              <h2 className="text-2xl font-bold text-white mb-4">Words Found</h2>
              <div className="max-h-64 overflow-y-auto">
                {validWords.length === 0 ? (
                  <p className="text-purple-200 text-center py-4">No words found yet</p>
                ) : (
                  <div className="grid grid-cols-1 gap-2">
                    {validWords.map((word, index) => (
                      <div
                        key={index}
                        className="bg-white/20 rounded-lg p-2 text-center"
                      >
                        <span className="text-white font-semibold">{word}</span>
                        <span className="text-purple-200 text-sm ml-2">
                          (+{calculateScore(word)} pts)
                        </span>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            </div>

            {/* Instructions */}
            <div className="bg-white/10 backdrop-blur-sm rounded-2xl p-6 shadow-2xl">
              <h2 className="text-2xl font-bold text-white mb-4">How to Play</h2>
              <ul className="text-purple-200 space-y-2 text-sm">
                <li>• Click letters to build words</li>
                <li>• Words must be at least 3 letters</li>
                <li>• Longer words give more points</li>
                <li>• Use keyboard for faster play</li>
                <li>• Press Enter to submit</li>
                <li>• Press Backspace to delete</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
