// Extended dictionary with common English words
const DICTIONARY = [
  // 3-letter words
  'cat', 'dog', 'run', 'sun', 'fun', 'car', 'bat', 'hat', 'rat', 'mat',
  'cup', 'top', 'pop', 'hop', 'map', 'cap', 'tap', 'gap', 'lap', 'nap',
  'pen', 'ten', 'hen', 'men', 'den', 'net', 'pet', 'set', 'wet', 'get',
  'big', 'dig', 'fig', 'pig', 'wig', 'bag', 'tag', 'wag', 'lag', 'rag',
  'box', 'fox', 'mix', 'fix', 'six', 'bus', 'cut', 'but', 'put', 'nut',
  
  // 4-letter words
  'word', 'game', 'play', 'time', 'home', 'love', 'life', 'work', 'hand',
  'part', 'head', 'back', 'good', 'long', 'make', 'take', 'come', 'give',
  'find', 'know', 'want', 'look', 'feel', 'tell', 'call', 'help', 'move',
  'turn', 'show', 'keep', 'hold', 'open', 'walk', 'talk', 'read', 'hear',
  'stop', 'start', 'book', 'door', 'room', 'tree', 'fire', 'water', 'food',
  
  // 5-letter words
  'house', 'world', 'place', 'right', 'great', 'small', 'think', 'where',
  'every', 'still', 'after', 'first', 'never', 'these', 'other', 'which',
  'their', 'would', 'there', 'could', 'state', 'while', 'being', 'again',
  'under', 'might', 'three', 'years', 'group', 'party', 'money', 'music',
  'light', 'night', 'today', 'happy', 'sorry', 'thank', 'heart', 'start',
  
  // 6+ letter words
  'people', 'family', 'friend', 'school', 'number', 'letter', 'mother',
  'father', 'sister', 'brother', 'change', 'follow', 'around', 'before',
  'should', 'through', 'between', 'another', 'without', 'nothing', 'something',
  'everything', 'anything', 'someone', 'everyone', 'anyone', 'somewhere',
  'everywhere', 'anywhere', 'because', 'however', 'therefore', 'although',
  'beautiful', 'wonderful', 'important', 'different', 'possible', 'available',
  'necessary', 'interesting', 'excellent', 'amazing', 'fantastic', 'incredible'
];

export function generateLetters(): string[] {
  // Generate letters with a good mix of vowels and consonants
  const vowels = 'AEIOU';
  const consonants = 'BCDFGHJKLMNPQRSTVWXYZ';
  const letters: string[] = [];
  
  // Ensure we have at least 2-3 vowels
  for (let i = 0; i < 3; i++) {
    letters.push(vowels[Math.floor(Math.random() * vowels.length)]);
  }
  
  // Fill the rest with consonants and some additional vowels
  for (let i = 0; i < 9; i++) {
    if (Math.random() < 0.3) {
      // 30% chance for vowel
      letters.push(vowels[Math.floor(Math.random() * vowels.length)]);
    } else {
      // 70% chance for consonant
      letters.push(consonants[Math.floor(Math.random() * consonants.length)]);
    }
  }
  
  // Shuffle the array
  for (let i = letters.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [letters[i], letters[j]] = [letters[j], letters[i]];
  }
  
  return letters;
}

export function isValidWord(word: string): boolean {
  if (word.length < 3) return false;
  return DICTIONARY.includes(word.toLowerCase());
}

export function calculateScore(word: string): number {
  // Base score is word length
  let score = word.length;
  
  // Bonus points for longer words
  if (word.length >= 6) score += 3;
  else if (word.length >= 5) score += 2;
  else if (word.length >= 4) score += 1;
  
  return score;
}

export function getWordHint(letters: string[]): string {
  // Find a valid word that can be made from the available letters
  const availableLetters = letters.join('').toLowerCase();
  
  for (const word of DICTIONARY) {
    if (word.length >= 4 && canMakeWord(word, availableLetters)) {
      return word.toUpperCase();
    }
  }
  
  return '';
}

function canMakeWord(word: string, availableLetters: string): boolean {
  const letterCount: { [key: string]: number } = {};
  
  // Count available letters
  for (const letter of availableLetters) {
    letterCount[letter] = (letterCount[letter] || 0) + 1;
  }
  
  // Check if word can be made
  for (const letter of word) {
    if (!letterCount[letter] || letterCount[letter] === 0) {
      return false;
    }
    letterCount[letter]--;
  }
  
  return true;
}
